@import "../constants/mixins";
@import "../constants/spaces";
@import "../constants/fonts";

.post-container {
  padding: $huge;

  .post__title {
    margin-bottom: $medium;
    color: #375195;
    font-size: 48px;
  }

  .post__subtitle {
    margin-bottom: $medium;
    font-size: 32px;
  }

  .post__paragraph {
    margin-bottom: $medium;
    font-size: $xxLarge;
    text-align: justify;
  }

  .post__paper {
    @include sm {
      background: #fafafa;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.3),
        0 0 300px 25px rgba(222, 198, 122, 0.7) inset;
      padding: $xxxHuge $huge;
      margin: 0 auto;
      position: relative;
      max-width: 900px;
      z-index: 0;

      &:before,
      &:after {
        content: "";
        background: #fafafa;
        box-shadow: 0 0 8px rgba(0, 0, 0, 0.2),
          inset 0 0 300px rgba(222, 198, 122, 0.7);
        height: 100%;
        width: 100%;
        position: absolute;
        z-index: -2;
        transition: 0.5s;
      }

      &:before {
        left: 0;
        top: 2px;
        transform: rotate(-1deg);
      }

      &:after {
        right: -3px;
        top: 0px;
      }
    }
    @include md {
      padding: $xxxHuge;
    }
  }
}
